* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

/* Header */
header {
  background-color: #56a3a6;
  color: #bfc0c0;
  text-align: center;
  font-family: 'Courier New', Courier, monospace;
  padding: 30px;
  display: flex;
  justify-content: space-between;
}

#avatar {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin-bottom: 2%;
}

.navList li {
  list-style-type: none;
  font-size: 30px;
  display: inline;
  margin-right: 30px;
}

li:hover {
  color: black;
  transition: background-color, color .5s;
  cursor: pointer;
}

.active {
  color: black;
}

/* Project Page */
.projectPage {
  background-color: #084c61;
  height: 73.5vh;
}

.projectTiles {
  display: flex;
  flex-wrap: wrap;
  padding: 60px;
}

.projectContainer {
  position: relative;
}

.textBlock {
  position: absolute;
  top: 50%;
  left: 32%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 20px;
}

.textBlock:hover {
  background-color: white;
  transition: background-color .5s, color .5s;
  cursor: pointer;
}

a:visited {
  text-decoration: none;
  color: #bfc0c0;
}

.projectLinks a:hover {
  color: rgb(87, 212, 15);
  transition: color .5s;
}

.projectImg {
  height: 250px;
  width: 450px;
  padding: 20px;
  transition: opacity .5s;
}

.projectImg:hover {
  opacity: 0.2;
  transition: opacity .5s;
}

/* About Me Section */
.aboutMe {
  padding: 40px;
  font-size: 30px;
  height: 73.5vh;
  color: whitesmoke;
  background-color: #084c61;
}

/* Navigator */
.navigator {
  background-color: #084c61;
  text-align: left;
  font-size: 30px;
  color: white;
  padding: 20px;
}

/* Form */
form {
  padding: 20px;
  margin: 0 auto;
  padding-bottom: 2em;
  background-color: #084c61;
  font-size: 18px;
  color: white;
  height: 73.5vh;
}

#name,
#email {
  margin-bottom: 1%;
}

#formSubmit {
  margin-top: 1%;
}

input,
textarea {
  margin: 10px 0 0 0;
  width: 40%;
  min-height: 2em;
  font-size: 16px;
}

/* Footer */
.footer {
  width: 100%;
  height: 100px;
  background-color: #084c61;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 10px;
  margin-top: 200px;
  position: fixed;
  bottom: 0;
}

.socialMedia svg {
  color: gray;
  margin: 20px;
  font-size: 50px;
  cursor: pointer;
}

